import React, { useCallback, useEffect, useState } from "react";
import swal from "sweetalert";
import { connect } from "react-redux";
import classNames from "./Keypad.module.css";

import * as reduxFunctions from "../../store/actions/index";

const Keypad = (props) => {
  const [indexPosition, setIndexPosition] = useState("topRow");

  const { closing, inputDisable } = props;

  let filteredBr = props.bR.filter((n) => typeof n === "number");
  let filteredTr = props.tR.filter((n) => typeof n === "number");

  const onKeyPress = useCallback(
    (key) => {
      if (!props.input) {
        if (!inputDisable) {
          if (filteredBr.length === 3) {
            swal(
              "All are set!",
              "Please press the start button to continue",
              "success"
            );
          } else {
            if (filteredTr.length === 2) {
              setIndexPosition("bottomRow");
            }
            props.onPress(key, indexPosition);
          }
        } else {
          swal(
            "Wrong action!",
            "Please end the current multiplication to start a new one!",
            "error"
          );
        }
      } else {
        props.inputPractice(key, props.r);
      }
    },
    [props, filteredTr, filteredBr, indexPosition, inputDisable]
  );

  const deleteDigit = useCallback(() => {
    if (filteredTr.length === 0 && filteredBr.length === 0) {
      swal("Sorry!", "No items to delete!", "error");
    } else {
      if (filteredBr.length === 0) {
        props.deleteDigit("topRow");
        setIndexPosition("topRow");
      } else {
        props.deleteDigit("bottomRow");
      }
    }
  }, [props, filteredTr, filteredBr]);

  const multiply = useCallback(() => {
    if (!inputDisable) {
      if (indexPosition === "topRow" && filteredTr.length !== 0) {
        setIndexPosition("bottomRow");
      } else if (indexPosition === "bottomRow" && filteredBr.length !== 0) {
        swal("Wrong action!", "You are already multiplying!", "error");
      } else if (filteredTr.length === 0 && filteredBr.length === 0) {
        swal(
          "Wrong action!",
          "Please add values in the top row first!",
          "error"
        );
      }
    } else {
      swal(
        "Wrong action!",
        "Please end the current multiplication to start a new one!",
        "error"
      );
    }
  }, [filteredTr, filteredBr, indexPosition, inputDisable]);

  useEffect(() => {
    if (closing) {
      setIndexPosition("topRow");
    }
    if (!inputDisable && filteredTr.length === 0) {
      setIndexPosition("topRow");
    }
    const logKey = (e) => {
      if (!props.input) {
        if (!inputDisable) {
          const str = e.code;
          const newStr = str.slice(0, -1);
          if (newStr === "Numpad" || newStr === "Digit") {
            if (!isNaN(e.key)) {
              onKeyPress(Number(e.key));
            }
          } else if (e.code === "NumpadMultiply") {
            multiply();
          } else if (e.code === "Backspace") {
            deleteDigit();
          } else if (e.code === "Delete") {
            deleteDigit();
          } else if (e.code === "Enter" || e.code === "NumpadEnter") {
            swal("All are set!", "We are starting", "success");
          } else {
            swal(
              "Wrong action!",
              "Please enter only numbers and you can use multiply from number pad!",
              "error"
            );
          }
        }
      } else {
        const str = e.code;
        const newStr = str.slice(0, -1);
        if (newStr === "Numpad" || newStr === "Digit") {
          if (!isNaN(e.key)) {
            props.inputPractice(Number(e.key), props.r);
          }
        } else if (e.code === "ArrowRight") {
        } else {
          swal("Wrong action!", "Please enter only numbers!", "error");
        }
      }
    };

    document.addEventListener("keydown", logKey);
    return () => {
      document.removeEventListener("keydown", logKey);
    };
  }, [
    closing,
    inputDisable,
    deleteDigit,
    multiply,
    onKeyPress,
    filteredTr,
    props,
  ]);
  return (
    <div className={classNames.container}>
      <div className={classNames.digits}>
        <button
          className={classNames.digit}
          onClick={() => {
            onKeyPress(7);
          }}
        >
          7
        </button>
        <button
          className={classNames.digit}
          onClick={() => {
            onKeyPress(8);
          }}
        >
          8
        </button>
        <button
          className={classNames.digit}
          onClick={() => {
            onKeyPress(9);
          }}
        >
          9
        </button>
        <button
          className={classNames.digit}
          onClick={() => {
            onKeyPress(4);
          }}
        >
          4
        </button>
        <button
          className={classNames.digit}
          onClick={() => {
            onKeyPress(5);
          }}
        >
          5
        </button>
        <button
          className={classNames.digit}
          onClick={() => {
            onKeyPress(6);
          }}
        >
          6
        </button>
        <button
          className={classNames.digit}
          onClick={() => {
            onKeyPress(1);
          }}
        >
          1
        </button>
        <button
          className={classNames.digit}
          onClick={() => {
            onKeyPress(2);
          }}
        >
          2
        </button>
        <button
          className={classNames.digit}
          onClick={() => {
            onKeyPress(3);
          }}
        >
          3
        </button>
        <button
          className={classNames.digit}
          onClick={() => {
            multiply();
          }}
        >
          <i className="fas fa-times"></i>
        </button>
        <button
          className={classNames.digit}
          onClick={() => {
            onKeyPress(0);
          }}
        >
          0
        </button>
        <button
          className={classNames.digit}
          onClick={() => {
            if (props.btnHide) {
              deleteDigit();
            }
          }}
        >
          <i className="fas fa-backspace"></i>
        </button>
      </div>
    </div>
  );
};

const mapStatetoProps = (state) => {
  return {
    tR: state.number.topRow,
    bR: state.number.bottomRow,
    iFT: state.numpad.inputFocusTop,
    iFB: state.numpad.inputFocusBottom,
    btnHide: state.number.btnHide,
    inputDisable: state.number.inputDisable,
    closing: state.result.closing,
    input: state.pMode.input,
    r: state.multi.answer,
  };
};
const mapDispatchtoProps = (dispatch) => {
  return {
    onPress: (key, changeFn) =>
      dispatch(reduxFunctions.keypress(key, changeFn)),
    multiply: () => dispatch(reduxFunctions.multiply()),
    deleteDigit: (position) => dispatch(reduxFunctions.deleteDigit(position)),
    inputPractice: (key, r) => dispatch(reduxFunctions.inputPractice(key, r)),
  };
};

export default connect(mapStatetoProps, mapDispatchtoProps)(Keypad);
