import React from "react";
import classNames from "../../containers/NumberBoard/Board.module.css";

const Row = (props) => {
  let classes;
  if (props.grid) {
    classes = `${classNames.carry} ${classNames.c3} ${classNames.brc}`;
  } else {
    classes = `${classNames.carry} ${classNames.c3}`;
  }
  return (
    <>
      <div className={`${classNames.row}`}>
        <div className={classes}>
          <h2>{null}</h2>
        </div>
        <div className={classes}>
          <h2>{null}</h2>
        </div>
        <div className={classes}>
          {props.n[2] ? (
            <h2 style={{ backgroundColor: "#ffffc0" }}>{props.n[2]}</h2>
          ) : null}
        </div>
        <div className={classes}>
          {props.n[1] ? (
            <h2 style={{ backgroundColor: "#ffffc0" }}>{props.n[1]}</h2>
          ) : null}
        </div>
        <div className={classes}>
          {props.n[0] ? (
            <h2 style={{ backgroundColor: "#ffffc0" }}>{props.n[0]}</h2>
          ) : null}
        </div>
        <div className={classes}>{null}</div>
      </div>
    </>
  );
};

export default Row;
