import React, { useCallback, useEffect } from "react";
import classNames from "./Buttons.module.css";
import { connect } from "react-redux";
import swal from "sweetalert";
import * as reduxFunctions from "../../store/actions/index";
import randomNumberGenerate from "../../store/reducers/Utility/randomNumber";

const Buttons = (props) => {
  const tR = props.tR;
  const bR = props.bR;

  const { btnHide, closing } = props;

  const starting = useCallback(() => {
    const tL = props.tR.join("").length;
    const bL = props.bR.join("").length;

    if (tL < bL) {
      swal(
        "Sorry!",
        "You can't add lower digits in the top than the bottom",
        "error"
      );
    } else {
      if (!props.bR[2]) {
        (async function () {
          const topRow = await randomNumberGenerate(props.dT);
          const bottomRow = await randomNumberGenerate(props.dB);
          props.startMulti(topRow, bottomRow);
        })();
      } else {
        props.startMulti(props.tR, props.bR);
      }
    }
  }, [props]);

  const ending = useCallback(() => {
    props.endMulti();
  }, [props]);

  const nextSteps = useCallback(() => {
    props.goNext(props.c, props.carries, props.results);
  }, [props]);

  const prevSteps = useCallback(() => {
    props.goPrev(props.c, props.results);
  }, [props]);

  useEffect(() => {
    if (closing) {
      ending();
    }

    const logKey = (e) => {
      if (e.key === "Enter") {
        if (btnHide) {
          starting();
        }
      }
      if (e.key === "Escape") {
        if (!btnHide) {
          ending();
        }
      }
      if (e.key === "ArrowRight") {
        if (!btnHide) {
          nextSteps();
        }
      }
      if (e.key === "ArrowLeft") {
        if (!btnHide) {
          prevSteps();
        }
      }
    };

    document.addEventListener("keydown", logKey);
    return () => {
      document.removeEventListener("keydown", logKey);
    };
  }, [tR, bR, btnHide, closing, starting, ending, nextSteps, prevSteps]);

  return (
    <>
      <div className={classNames.container}>
        <div className={classNames.inner}>
          <button onClick={prevSteps} disabled={props.btnHide ? true : false}>
            <i className="fas fa-backward"></i>
          </button>
          {props.btnHide ? (
            <button onClick={starting} disabled={!props.btnHide}>
              <i className="fas fa-play"></i>
            </button>
          ) : (
            <button onClick={nextSteps}>
              <i className="fas fa-forward"></i>
            </button>
          )}

          <button onClick={ending} disabled={props.btnHide}>
            <i className="fas fa-stop"></i>
          </button>
          <button
            onClick={() => {
              props.setToggleKeypad();
            }}
            id="keypadTrigger"
          >
            <i className="fas fa-keyboard"></i>
          </button>
        </div>
      </div>
    </>
  );
};
const mapStatetoProps = (state) => {
  return {
    tR: state.number.topRow,
    bR: state.number.bottomRow,
    dT: state.number.trd,
    dB: state.number.brd,
    btnHide: state.number.btnHide,
    fR: state.result.finalResult,
    carries: state.multi.answer.carries,
    results: state.multi.answer,
    closing: state.result.closing,
  };
};

const mapDispatchtoProps = (dispatch) => {
  return {
    startMulti: (t, b) => dispatch(reduxFunctions.startMultiplication(t, b)),
    endMulti: () => dispatch(reduxFunctions.endMultiplication()),
    setToggleKeypad: () => dispatch(reduxFunctions.keypadToggle()),
    goPrev: (c, r) => dispatch(reduxFunctions.goPrev(c, r)),
    goNext: (p, c, r) => dispatch(reduxFunctions.goNext(p, c, r)),
  };
};

export default connect(mapStatetoProps, mapDispatchtoProps)(Buttons);
