import React from "react";
import classNames from "../ExplainationBoard.module.css";

const Learn = (props) => {
  return (
    <div className={classNames.text}>
      <h3
        dangerouslySetInnerHTML={{
          __html: props.title.show
            ? props.title.title
            : "Generate random numbers or input numbers to start",
        }}
      ></h3>
      <div>
        {Object.values(props.texts).map((c, i) => {
          return <p dangerouslySetInnerHTML={{ __html: c }} key={i}></p>;
        })}
      </div>
    </div>
  );
};

export default Learn;
