import React, { useCallback, useEffect } from "react";
import classNames from "./Buttons.module.css";
import { connect } from "react-redux";
import swal from "sweetalert";
import * as reduxFunctions from "../../store/actions/index";
import randomNumberGenerate from "../../store/reducers/Utility/randomNumber";

const Buttons = (props) => {
  const tR = props.tR;
  const bR = props.bR;

  const { btnHide, closing, nxtBtn } = props;

  const starting = useCallback(() => {
    const tL = props.tR.join("").length;
    const bL = props.bR.join("").length;

    if (tL < bL) {
      swal(
        "Sorry!",
        "You can't add lower digits in the top than the bottom",
        "error"
      );
    } else {
      if (!props.bR[2]) {
        (async function () {
          const topRow = await randomNumberGenerate(props.dT);
          const bottomRow = await randomNumberGenerate(props.dB);
          props.startPractice(topRow, bottomRow);
        })();
      } else {
        props.startPractice(props.tR, props.bR);
      }
    }
  }, [props]);

  const ending = useCallback(() => {
    props.endPractice();
  }, [props]);

  const nextSteps = useCallback(() => {
    props.nextPractice(props.results);
  }, [props]);

  useEffect(() => {
    if (closing) {
      ending();
    }

    const logKey = (e) => {
      if (e.key === "Enter") {
        if (btnHide) {
          starting();
        }
      }
      if (e.key === "Escape") {
        if (!btnHide) {
          ending();
        }
      }
      if (e.key === "ArrowRight") {
        if (!btnHide && !nxtBtn) {
          nextSteps();
        }
      }
    };

    document.addEventListener("keydown", logKey);
    return () => {
      document.removeEventListener("keydown", logKey);
    };
  }, [tR, bR, btnHide, closing, nxtBtn, starting, ending, nextSteps]);

  return (
    <>
      <div className={classNames.container}>
        <div className={classNames.inner}>
          <button
            onClick={starting}
            style={!props.btnHide ? { display: "none" } : null}
          >
            <i className="fas fa-play"></i>
          </button>
          <button
            onClick={nextSteps}
            style={props.btnHide ? { display: "none" } : null}
            id="nextPractice"
            disabled={nxtBtn}
          >
            <i className="fas fa-forward"></i>
          </button>

          <button onClick={ending} disabled={props.btnHide}>
            <i className="fas fa-stop"></i>
          </button>
          <button
            onClick={() => {
              props.setToggleKeypad();
            }}
            id="keypadTrigger"
          >
            <i className="fas fa-keyboard"></i>
          </button>
        </div>
      </div>
    </>
  );
};
const mapStatetoProps = (state) => {
  return {
    tR: state.number.topRow,
    bR: state.number.bottomRow,
    dT: state.number.trd,
    dB: state.number.brd,
    btnHide: state.pMode.btnHide,
    nxtBtn: state.pMode.nxtBtn,
    results: state.multi.answer,
  };
};

const mapDispatchtoProps = (dispatch) => {
  return {
    startPractice: (t, b) => dispatch(reduxFunctions.startPractice(t, b)),
    endPractice: () => dispatch(reduxFunctions.endPractice()),
    nextPractice: (r) => dispatch(reduxFunctions.nextPractice(r)),
    setToggleKeypad: () => dispatch(reduxFunctions.keypadToggle()),
  };
};

export default connect(mapStatetoProps, mapDispatchtoProps)(Buttons);
