import React, { useEffect } from "react";
import { connect } from "react-redux";
import Exam from "./Exam/Exam";
import classNames from "./ExplainationBoard.module.css";
import Learn from "./Learn/Learn";
import Practice from "./Practice/Practice";

const ExplainationBoard = (props) => {
  useEffect(() => {
    const explainerContainer = document.getElementById("explainerContainer");
    explainerContainer.scroll({
      top: explainerContainer.scrollHeight,
      behavior: "smooth",
    });
  }, [props.texts]);

  let board;
  if (props.modes.learn) {
    board = <Learn title={props.title} texts={props.texts} />;
  } else if (props.modes.practice) {
    board = (
      <Practice title={props.practiceTitle} message={props.practiceMessage} />
    );
  } else if (props.modes.exam) {
    board = <Exam />;
  }

  return (
    <div className={classNames.container}>
      <div className={classNames.inner} id="explainerContainer">
        {board}
      </div>
    </div>
  );
};
const mapStatetoProps = (state) => {
  return {
    title: state.number.boardTitle,
    practiceTitle: state.pMode.boardTitle,
    texts: state.result.allTexts,
    practiceMessage: state.pMode.message,
    modes: state.menu.modes,
  };
};
export default connect(mapStatetoProps)(ExplainationBoard);
