import * as actionTypes from "./actions";

export const boardSwitch = (mod) => {
  return {
    type: actionTypes.BOARDSWITCH,
    mod: mod,
  };
};

export const startPractice = (topRow, bottomRow) => {
  return {
    type: actionTypes.PRACTICESTART,
    topRow: topRow,
    bottomRow: bottomRow,
  };
};
export const endPractice = () => {
  return {
    type: actionTypes.PRACTICEEND,
  };
};
export const nextPractice = (r) => {
  return {
    type: actionTypes.PRACTICENEXT,
    r: r,
  };
};
export const inputPractice = (key, r) => {
  return {
    type: actionTypes.PRACTICEINPUT,
    key: key,
    r: r,
  };
};
export const messagePractice = (m, w) => {
  return {
    type: actionTypes.PRACTICEMESSAGE,
    m: m,
    w: w,
  };
};
