import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import * as reduxFunctions from "../../store/actions/index";
import Buttons from "../ActionButtons/Buttons";
import ButtonsPractice from "../ActionButtonPractice/Buttons";
import ExplainationBoard from "../ExplainationBoard/ExplainationBoard";
import Board from "../NumberBoard/Board";
import Practice from "../PracticeBoard/PracticeBoard";
import ExamBoard from "../ExamBoard/ExamBoard";
import classNames from "./Layout.module.css";
import Header from "../Header/Header";
import Keypad from "../Keypad/Keypad";
import Bar from "../../components/Rightsidebar/Bar";

const Layout = (props) => {
  const [icon, setIcon] = useState(<i className="fas fa-bars"></i>);

  const sideMenuToggleClick = () => {
    props.sideMenuToggleFn();
  };
  const {
    keypadHide,
    toggleKeypad,
    keypadShow,
    sideMenuToggle,
    sideMenuClose,
    inputDisable,
  } = props;

  useEffect(() => {
    if (!sideMenuToggle) {
      setIcon(<i className="fas fa-bars"></i>);
    } else {
      setTimeout(() => {
        setIcon(<i className="fas fa-times"></i>);
      }, 300);
    }
    const numberPad = document.getElementById("numberPad");
    const sideBar = document.getElementById("sidebar");
    const sideBarTrigger = document.getElementById("sideBarTrigger");
    const mvmenu = document.getElementById("mvmenu");
    const keypadTrigger = document.getElementById("keypadTrigger");
    const board_container = document.getElementById("board_container");
    const nextPractice = document.getElementById("nextPractice");

    const keypadShowFn = () => {
      if (!inputDisable) keypadShow();
    };

    const documentClickFn = (e) => {
      if (inputDisable) {
        if (nextPractice) {
          if (
            e.target !== numberPad &&
            !numberPad.contains(e.target) &&
            e.target !== keypadTrigger &&
            !keypadTrigger.contains(e.target) &&
            e.target !== nextPractice &&
            !nextPractice.contains(e.target)
          ) {
            if (toggleKeypad) keypadHide();
          }
        } else {
          if (
            e.target !== numberPad &&
            !numberPad.contains(e.target) &&
            e.target !== keypadTrigger &&
            !keypadTrigger.contains(e.target)
          ) {
            if (toggleKeypad) keypadHide();
          }
        }
      } else {
        if (
          e.target !== numberPad &&
          !numberPad.contains(e.target) &&
          e.target !== keypadTrigger &&
          !keypadTrigger.contains(e.target) &&
          e.target !== board_container &&
          !board_container.contains(e.target)
        ) {
          if (toggleKeypad) keypadHide();
        }
      }

      if (
        e.target !== sideBar &&
        !sideBar.contains(e.target) &&
        e.target !== sideBarTrigger &&
        !sideBarTrigger.contains(e.target) &&
        e.target !== mvmenu &&
        !mvmenu.contains(e.target)
      ) {
        if (sideMenuToggle) sideMenuClose();
      }
    };

    document.addEventListener("click", documentClickFn);
    board_container.addEventListener("click", keypadShowFn);
    return () => {
      board_container.removeEventListener("click", keypadShowFn);
      document.removeEventListener("click", documentClickFn);
    };
  }, [
    keypadHide,
    toggleKeypad,
    keypadShow,
    sideMenuToggle,
    sideMenuClose,
    inputDisable,
  ]);

  let board, actionButtons;
  if (props.modes.learn) {
    board = <Board />;
    actionButtons = <Buttons />;
  } else if (props.modes.practice) {
    board = <Practice />;
    actionButtons = <ButtonsPractice />;
  } else if (props.modes.exam) {
    board = <ExamBoard />;
  }

  return (
    <>
      <div className={classNames.container}>
        <Header />
        <div className={classNames.inner}>
          <div className={classNames.board_container} id="board_container">
            {board}
          </div>
          <div className={classNames.explainer_container}>
            <ExplainationBoard />
          </div>
        </div>
      </div>
      <div
        className={`${classNames.number_pad_container} ${
          props.toggleKeypad
            ? classNames.number_pad_container_grow
            : classNames.number_pad_container_shrink
        }`}
        id="numberPad"
      >
        <Keypad />
      </div>
      {actionButtons}
      <div className={classNames.mvlogo}>
        <img src="https://zoomcbd.com/math/logo.svg" alt="Logo" />
      </div>
      <div
        className={classNames.mvmenu}
        onClick={() => {
          sideMenuToggleClick();
        }}
        id="mvmenu"
      >
        {icon}
      </div>
      <Bar />
    </>
  );
};
const mapStatetoProps = (state) => {
  return {
    sideMenuToggle: state.menu.sideMenuToggle,
    toggleKeypad: state.numpad.toggleKeypad,
    inputDisable: state.number.inputDisable,
    modes: state.menu.modes,
  };
};
const mapDispatchtoProps = (dispatch) => {
  return {
    keypadHide: () => dispatch(reduxFunctions.keypadHide()),
    keypadShow: () => dispatch(reduxFunctions.keypadShow()),
    sideMenuToggleFn: () => dispatch(reduxFunctions.sideMenuToggle()),
    sideMenuClose: () => dispatch(reduxFunctions.sideMenuClose()),
  };
};
export default connect(mapStatetoProps, mapDispatchtoProps)(Layout);
