import * as actionTypes from "../actions/actions";

const initialState = {
  boardTitle: {
    title: "",
    show: false,
  },
  btnHide: true,
  nxtBtn: true,
  input: false,
  message: "",
  tr: null,
  br: null,
  c: null,
  p: null,
  carry: null,
  correctCarryCell: false,
  valueEnter: false,
};

const practiceMode = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.PRACTICESTART:
      const tR = action.topRow.join("");
      const bR = action.bottomRow.join("");
      const n1 = action.topRow[tR.length - 1];
      const n2 = action.bottomRow[bR.length - 1];
      const ans = n1 * n2;
      // What will be the carry and result of <span>${action.topRow[2]}</span> x <span>${action.bottomRow[2]}</span> ?
      return {
        ...state,
        boardTitle: {
          title: `We will muiltiply <span>${tR}</span> and <span>${bR}</span> step by step together. Our first step will be entering the correct values in the  highlighted area. And then <span>click the next button to continue.</span>`,
          show: true,
        },
        btnHide: false,
        tr: tR.length - 1,
        br: bR.length - 1,
        c: 0,
        p: 0,
        input: true,
        message: `${n1} X ${n2} = ${ans}, here we have carry ${Math.floor(
          ans / 10
        )} and result ${Math.floor(
          ans % 10
        )}. Click on which cell the carry will go ?`,
        correctCarryCell: 4,
      };
    case actionTypes.PRACTICENEXT:
      state.valueEnter = false;
      document
        .getElementById("carryInputContainer")
        .childNodes.forEach((c) => (c.style.backgroundColor = "transparent"));
      let t, b, c, p, cc;
      t = state.tr;
      b = state.br;
      c = state.c;
      p = state.p;
      if (t === 0 && b >= 0) {
        t = action.r.tR.length - 1;
        b--;
      } else {
        t--;
      }
      if (c === action.r.tR.length - 1) {
        c = 0;
        p++;
      } else {
        c++;
      }
      switch (state.c + 1) {
        case 0:
          cc = 4;
          break;
        case 1:
          cc = 3;
          break;
        case 2:
          cc = 2;
          break;
        default:
          cc = 4;
          break;
      }
      let prevCarry = action.r.carries[state.c][state.p];
      let mult = action.r.tR[t] * action.r.bR[b];
      return {
        ...state,
        nxtBtn: true,
        tr: t,
        br: b,
        c: c,
        p: p,
        carry: null,
        message: `${action.r.tR[t]} X ${
          action.r.bR[b]
        } = ${mult} , Previously we had carry ${prevCarry} after adding that we have ${mult} + ${prevCarry} = ${
          mult + prevCarry
        } . Now, we have carry ${action.r.carries[c][p]} and result of `,
        correctCarryCell: cc,
      };
    case actionTypes.PRACTICEINPUT:
      let returnObj;
      if (state.valueEnter) {
        state.valueEnter = true;
        const correctCarry = action.r.carries[state.c][state.p];
        if (action.key === correctCarry) {
          action.asyncDispatch({ type: actionTypes.KEYPADHIDE });
          returnObj = {
            nxtBtn: false,
            carry: action.key,
            message: "Correct! Great let's continue. Click next.",
            correctCarryCell: false,
          };
        } else {
          returnObj = {
            nxtBtn: true,
            carry: action.key,
            message: "Wrong! Try again.",
            correctCarryCell: false,
          };
        }
      } else {
        action.asyncDispatch({ type: actionTypes.KEYPADHIDE });
        returnObj = {
          message: "Please select the cell first!",
        };
      }
      return {
        ...state,
        ...returnObj,
      };
    case actionTypes.PRACTICEMESSAGE:
      let m;
      if (action.w) {
        m = `${action.m} Now enter the value of it.`;
      } else {
        m = action.m;
      }
      return {
        ...state,
        message: m,
        valueEnter: action.w,
      };
    case actionTypes.PRACTICEEND:
      return {
        ...state,
        boardTitle: {
          title: "",
          show: false,
        },
        btnHide: true,
        nxtBtn: true,
        input: false,
        message: "",
        tr: null,
        br: null,
        c: null,
        p: null,
        carry: null,
        correctCarryCell: null,
      };
    default:
      return state;
  }
};

export default practiceMode;
